import lottie from 'lottie-web'
import { throttle } from 'lodash'
import danmu from './danmu.json'
import VConsole from 'vconsole'

import * as UFO from './animation/UFO.json'
import * as UFOOther from './animation/UFO_others.json'
import * as box from './animation/box.json'
import * as boxOther from './animation/box_others.json'
import * as airplane from './animation/airplane.json'
import * as airplaneOther from './animation/airplane_others.json'
import * as crown from './animation/crown.json'
import * as crownOther from './animation/crown_others.json'
import * as donut from './animation/donut.json'
import * as donutOther from './animation/donut_others.json'
import * as star_me from './animation/star_me.json'

const dataMap = {
  UFO,
  box,
  airplane,
  crown,
  donut,
  star_me
}

var vConsole = new VConsole()

const oneSecond = cb => throttle(cb, 1000)

const video = document.querySelector('video')

const doc = document.querySelector('.animation')

// 最后一次播放动画的时间
let lastTime = -1

const handleDanmu = () => {
  const currentTime = Math.round(video.currentTime)
  if (lastTime === currentTime) {
    return
  } else {
    lastTime = currentTime

    console.log('currentTime:', currentTime)
    const data = danmu.filter(d => Math.round(d.time) === currentTime)

    data.length > 0 && console.log('弹幕:', data)
    data.map(d => {
      sendDanmu(d)
    })
  }
}

const sendDanmu = d => {
  const anchor = window.document.createElement('div')
  const { left, top, width, height, animation } = d
  anchor.style.cssText = `position:absolute;left:${left}%;top:${top}%;width:${width}%;height:${height}%;zIndex:4;`
  doc.appendChild(anchor)

  lottie.loadAnimation({
    container: anchor,
    renderer: 'svg',
    loop: false,
    autoplay: true,
    animationData: dataMap[animation]
  })
}

document.getElementById('send').addEventListener('click', () => {
  const danmu = {
    top: document.getElementById('top').value,
    left: document.getElementById('left').value,
    width: document.getElementById('width').value,
    height: document.getElementById('height').value,
    animation: document.getElementById('animation').value
  }
  console.log('发送动画弹幕:', danmu)
  sendDanmu(danmu)
})

video.addEventListener('timeupdate', handleDanmu)
